html {
    height:100%;
  }
  
  body {
    margin:0;
  }
  
  .bg {
    -webkit-animation:slide 3s ease-in-out infinite alternate;
            animation:slide 3s ease-in-out infinite alternate;
    background-image: -webkit-linear-gradient(150deg, rgba(10, 92, 255, 0.733) 50%, #09f 50%);
    background-image: -o-linear-gradient(150deg, rgba(10, 92, 255, 0.733) 50%, #09f 50%);
    background-image: linear-gradient(-60deg, rgba(10, 92, 255, 0.733) 50%, #09f 50%);
    bottom:0;
    left:-50%;
    opacity:.5;
    position:fixed;
    right:-50%;
    top:0;
    z-index:-1;
  }
  
  .bg2 {
    -webkit-animation-direction:alternate-reverse;
            animation-direction:alternate-reverse;
    -webkit-animation-duration:4s;
            animation-duration:4s;
  }
  
  .bg3 {
    -webkit-animation-duration:5s;
            animation-duration:5s;
  }
  
  .content {
    background-color:transparent;
    border-radius:.25em;
    /*box-shadow:0 0 .25em rgba(0,0,0,.25);*/
    -webkit-box-sizing:border-box;
            box-sizing:border-box;
    left:50%;
    padding:10vmin;
    position:fixed;
    text-align:center;
    top:50%;
    -webkit-transform:translate(-50%, -50%);
        -ms-transform:translate(-50%, -50%);
            transform:translate(-50%, -50%);
  }
  
  h1 {
    font-family:monospace;
  }
  
  @-webkit-keyframes slide {
    0% {
      -webkit-transform:translateX(-25%);
              transform:translateX(-25%);
    }
    100% {
      -webkit-transform:translateX(25%);
              transform:translateX(25%);
    }
  }
  
  @keyframes slide {
    0% {
      -webkit-transform:translateX(-25%);
              transform:translateX(-25%);
    }
    100% {
      -webkit-transform:translateX(25%);
              transform:translateX(25%);
    }
  }


